* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

html {
  font-family: Arial, Helvetica, sans-serif;
}

.people {
  grid-template-columns: repeat(4, 200px);
  justify-content: center;
  gap: 40px 30px;
  padding: 30px 35px;
  display: grid;
}

.person {
  width: 200px;
  height: 231px;
}

.person__photo {
  position: relative;
}

.person__image {
  border-radius: 50%;
  width: 100%;
  height: 100%;
  margin-bottom: 8px;
}

.person__rank {
  text-align: center;
  color: #8b0000;
  background-color: #fff;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  width: 32px;
  height: 32px;
  font-size: 16px;
  font-weight: bold;
  display: flex;
  position: absolute;
  top: 84px;
  left: 158px;
}

.person__name {
  color: #2f4f4f;
  text-align: center;
  font-size: 20px;
  font-weight: bold;
  line-height: 23px;
  display: block;
}

.person__extra {
  display: none;
}

@media (width <= 500px) {
  .people {
    grid-template-columns: repeat(2, 200px);
  }
}

@media (width >= 750px) {
  .people {
    grid-template-columns: repeat(2, 200px);
  }
}

@media (width >= 1000px) {
  .people {
    grid-template-columns: repeat(4, 200px);
  }
}
